module Imports

open Fable.Core
open Fable.Core.JsInterop
open Fable.React
open Browser.Types

// Web3 providers
// --------------

[<Import("Web3Provider", from = "@ethersproject/providers")>]
type Web3Provider(provider :obj) = class end

type Web3ReactProviderProps =
    | GetLibrary of obj

type IWeb3Connector = interface end

type InjectedConnectorConfig =
    { supportedChainIds: int[] }

type WalletLinkConnectorConfig =
    { url: string
      supportedChainIds: int[]
      appName: string }

type WalletConnectConnectorConfig =
    { rpcUrl: string
      bridge: string
      qrcode: bool }

[<Import("InjectedConnector", from = "@web3-react/injected-connector")>]
type InjectedConnector(injectedConnectorConfig :InjectedConnectorConfig) =
    interface IWeb3Connector

[<Import("WalletLinkConnector", from = "@web3-react/walletlink-connector")>]
type WalletLinkConnector(walletLinkConnectorConfig :WalletLinkConnectorConfig) =
    interface IWeb3Connector

[<Import("WalletConnectConnector", from = "@web3-react/walletconnect-connector")>]
type WalletConnectConnector(walletConnectConnector :WalletConnectConnectorConfig) =
    interface IWeb3Connector

let inline web3ReactProvider (props : Web3ReactProviderProps list) (elems : ReactElement list) : ReactElement = 
    ofImport "Web3ReactProvider" "@web3-react/core"  (keyValueList CaseRules.LowerFirst props) elems

type UseWeb3ReactHook =
    { account: string option
      activate: IWeb3Connector -> unit
      active: bool
      chainId: int option
      deactivate: unit -> unit
      library: obj
      error: obj option
      setError: obj -> unit }

let useWeb3React :unit -> UseWeb3ReactHook = import "useWeb3React" "@web3-react/core"

type Ethers() =

    [<Emit("new $0.Contract($1, $2, $3)")>]
    member _.newContract(address :string, abi :obj, signer :obj) = jsNative

let ethers :Ethers = import "ethers" "ethers"

type FormatType = interface end

type FormatTypes =
    abstract full: FormatType

let formatTypes :FormatTypes = import "FormatTypes" "@ethersproject/abi"

[<Import("Interface", from = "@ethersproject/abi")>]
type Interface(abi :obj) =
    member _.format(formatType :FormatType) :obj = jsNative

type ConfirmedTransactionResponse =
    abstract blockHash: string
    abstract blockNumber: int
    abstract confirmations: int
    abstract gasUsed: obj
    abstract transactionHash: string
    abstract from: string

type TransactionResponse =
    abstract chainId: int
    abstract confirmations: int
    abstract gasLimit: obj
    abstract gasPrice: obj
    abstract hash: string
    abstract nonce: int
    abstract from: string
    abstract wait: unit -> JS.Promise<ConfirmedTransactionResponse>

type IBN =

    abstract eq :IBN -> bool

    abstract div :IBN -> IBN

    abstract ``mod`` :IBN -> IBN

type IWeb3Util =

    abstract toBN :int -> IBN

    abstract toBN :string -> IBN

    abstract soliditySha3 :obj -> string



