module ReactMarkdown =

    let inline markdown (elems :ReactElement list) : ReactElement =
        ofImport "default" "react-markdown" [] elems

module ReactJazzicon =

    type JazziconProps =
        | Diameter of int
        | Seed of int32

    let inline jazzicon (props :JazziconProps list) (elems :ReactElement list) : ReactElement =
        ofImport "default" "react-jazzicon" (keyValueList CaseRules.LowerFirst props) elems

