module Home.View

open Fable.Core
open Fable.Core.JsInterop
open Fable.React
open Fable.React.Props
open Types
open Common
open Global
open System.Globalization
open Imports
open Utils
open ViewComponents

let example1 =
    a [ClassName "profile-info"; Href "https://web3tr.ee/web3tree"; Target "_blank"]
        [div [ClassName "image-outer"]
            [div [ClassName "image"; Style [BackgroundImage ("url(https://web3tree.b-cdn.net/0x31f3bba9b71cb1d5e96cd62f0ba3958c034b55e92087_w388)")]] []]
         div [] [str "Web3Tree"]]

let example2 =
    a [ClassName "profile-info"; Href "https://web3tr.ee/-2567"; Target "_blank"]
        [div [ClassName "image-outer"]
            [div [ClassName "image"; Style [BackgroundImage ("url(/img/blockheads.png)")]] []]
         div [] [str "Blockheads"]]

let connect (web3 :UseWeb3ReactHook) dispatch =
    match web3.active with
    | false ->
        [div [ClassName "connect"]
            [ConnectButton { className = "button big-connect"; children = [str "Connect"] }
             p [] [str "Connect your account to edit your page"]]
         ]
    | true ->
        [div [ClassName "connected"]
            [jazziconForAddress 48 web3.account.Value
             p [] [b [] [str (niceAddress web3.account.Value)]]
             button [OnClick (fun _ -> dispatch (ChangePage (View web3.account.Value))); ClassName "button big-connect"] [str "View Page"]
             button [OnClick (fun _ -> dispatch (ChangePage (Edit web3.account.Value))); ClassName "button big-connect"] [str "Edit Page"]]]

let HomePage =
    FunctionComponent.Of ((fun (props : {| dispatch: Msg -> unit |}) ->
        let web3 = useWeb3React()
        div [ClassName "home"]
            [div [ClassName "home-hero"]
                [div [ClassName "inner"]
                    [img [Src "/img/tree.svg"]
                     h1 [] [str "Web3Tree"]
                     h2 [] [str "Your own permanent space in Web3"]]]
             yield! connect web3 props.dispatch
             section []
                [h1 [] [str "Featured Pages"]
                 div [ClassName "featured"] [example1; example2]]
             section [ClassName "how-to"]
                [h1 [] [str "How it Works"]
                 div [ClassName "card"]
                    [h2 [] [str "1. Connect your Account"]
                     p [] [str "Click the connect button and connect your desired account with Metamask, WalletConnect or Coinbase Wallet (More providers added soon!)"]]
                 div [ClassName "card"] 
                    [h2 [] [str "2. Edit your Page"]
                     p [] [str "Click the edit button and create your page. Choose your name, bio and add an unlimited number of links. The NFTs in your account will automatically be displayed. Choose one as your profile picture or hide any you don't want people to see."]]
                 div [ClassName "card"]
                    [h2 [] [str "3. Save your Page"]
                     p [] [str "Click the save button to save your page. You will be prompted by your wallet provider to confirm a transaction which will save your data permanently to the Ethereum blockchain. After your transaction is confirmed you will be able to view your page."]]
                 div [ClassName "card"]
                    [h2 [] [str "4. Share your URL"]
                     p [] [str "You can now share your URL on your social media profiles or wherever you want. Web3Tree will automatically create a unique URL for you, but if you want to customise one you can register an ENS address. If your ENS address is 'myname.eth' then the URL to your page will be 'https://web3tr.ee/myname'."]]]
             footer []
                [a [Href "https://blockheads.network"; Target "_blank"] [img [Src "/img/logoblock.png"]]
                 p [] [a [Href "https://blockheads.network"; Target "_blank"] [str "From the creators of Blockheads"]]]
             ]
   ), "HomePage", equalsButFunctions)

let root model common dispatch = HomePage {| dispatch = dispatch |}
